  /* YourNewFile.module.css */

.list {
    margin: 16px auto;
    width: 100%;
    max-width: 500px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
    transition: 0.3s ease-in-out;
    position: relative;
    overflow: hidden;
  }
  
  
  .dark-bg {
  padding: 1px;
  }
  
  .company-pos {
    padding-top: 15px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  
  }
  
  .list .company-logo {
    height: auto;
    width: 120px;
    object-fit:cover;
  }
  
  .list:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
  }
  
  .position {
    color: #222;
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 12px;
  }
  
  .position a {
    color: #222;
    font-weight: 600;
    text-decoration: none;
  }
  
  .position span {
    font-weight: 500;
  }
  
  .position .time {
    color: #555;
    font-size: 14px;
    font-weight: 500;
  }
  
  .position .company {
    font-weight: 600;
    font-size: 20px;
  }
  
  .position .ta {
    color: #555;
    font-size: 14px;
    font-weight: 500;
  }
  
  .work {
    
    padding-left: 20px;
    padding-right: 10px;
    color: #555;
    font-size: 14px;
    width: 100%;
    font-weight: 600;
    margin-top: 18px;
    margin-bottom: 18px;
  }
  
  .experience {
    margin-top: 12px;
    margin-bottom: 12px;
  }
  
  .title {
    color: #222;
    font-weight: 800;
    font-size: 23px;
    margin-bottom: 2.5rem;
    margin-top: 2.5rem;
    text-align: center;
  }
  
  .details {
    margin: 0 auto;
    position: relative;
  }
  
  .details ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-left: 20px;
  }
  
  /* Profile.module.css */

.list2 {
    padding-top: 15px;
    margin: 16px auto;
    width: 100%;
    max-width: 500px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
    transition: 0.3s ease-in-out;
    overflow: hidden;
    position: relative;
    
  }
  
  .list2:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
  }
  
  .pos {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .school-pos {
    
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .school-logo {
    height: auto;
    width: 150px;
    object-fit: cover;
    margin-right: 27px;
  }
  
  .position2 {
    color: #222;
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 18px;
  }
  
  .position2 .et {
    color: #222;
    font-weight: 600;
    margin-bottom: 27px;
  }
  
  .position2 .et .school {
    font-weight: 600;
    font-size: 18px;
  }
  
  .position2 .work2 {
    color: #222;
    font-weight: 500;
    font-size: 14px;
  }
  
  .position2 .time {
    color: #555;
    font-size: 14px;
    font-weight: 500;
  }
  
  .position2 .work2 {
    color: #555;
    width: 100%;
    font-weight: 400;
  }
  
  
  .position2 .field {
    font-weight: 500;
    font-size: 18px;
  }
  
  .experience2 {
    margin-top: 16px;
    margin-bottom: 16px;
    position: relative;
  }
  
  .title2 {
    color: #222;
    font-weight: 800;
    font-size: 23px;
    margin-bottom: 2.5rem;
    margin-top: 2.5rem;
    text-align: center;
  }
  
  .details2 {
    margin: 0 auto;
    width: 100%;
  }
  
  .details2 ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-left: 20px;
  }
  
  .timeline-container {
    position: absolute;
    left: -10px;
    top: 0;
    height: 100%;
  }
  
  .timeline {
    width: 10px;
    height: 100%;
    background-color: #d8bb3d;
    transform-origin: top;
  }
  
