/* İş Deneyimi */
@media screen and (max-width: 768px) {
    .list {
      max-width: 100%;
    }
  
    .position,
    .position a,
    .position .time,
    .position .company,
    .position .ta,
    .work,
    .experience,
    .title,
    .details ul {
      text-align: center;
      margin-left: 0;
    }
  
    .position {
      font-size: 18px;
      margin-bottom: 10px;
    }
  
    .position .time {
      font-size: 12px;
      margin-bottom: 10px;
    }
  
    .position .company,
    .position .ta {
      font-size: 16px;
    }
  
    .work {
      font-size: 12px;
      margin-top: 12px;
      margin-bottom: 12px;
    }
  
    .experience {
      margin-top: 8px;
      margin-bottom: 8px;
    }
  
    .title {
      font-size: 20px;
      margin-bottom: 1.5rem;
      margin-top: 1.5rem;
    }
  
    .details ul {
      margin-left: 0;
    }
  }
  
  /* Eğitim Bilgisi */
  @media screen and (max-width: 768px) {
    .list2 {
      max-width: 100%;
    }
  
    .position2,
    .position2 .et,
    .position2 .et .school,
    .position2 .work2,
    .position2 .time,
    .position2 .field,
    .experience2,
    .title2,
    .details2 ul {
      text-align: center;
      margin-left: 0;
    }
  
    .position2 {
      font-size: 16px;
      margin-bottom: 10px;
    }
  
    .position2 .et {
      margin-bottom: 15px;
    }
  
    .position2 .et .school {
      font-size: 16px;
    }
  
    .position2 .work2 {
      font-size: 12px;
      width: 100%;
      font-weight: 400;
    }
  
    .position2 .time {
      font-size: 12px;
      margin-bottom: 10px;
    }
  
    .position2 .field {
      font-size: 16px;
    }
  
    .experience2 {
      margin-top: 8px;
      margin-bottom: 8px;
    }
  
    .title2 {
      font-size: 20px;
      margin-bottom: 1.5rem;
      margin-top: 1.5rem;
    }
  
    .details2 ul {
      margin-left: 0;
    }
  
    .timeline-container {
      display: none;
    }
  }
  
  