
.contact {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.title h2 {
    color: #222;
    text-align: center;
    font-size: 23px;
    font-weight: 800;
    margin-top: 2.5rem;

}

.contact-form {
    grid-template-columns: repeat(minmax(500px), 1fr);
    padding-top: 50px;
}

.content {
    align-self: start;
}

.content p {
    font-size: 14px;
    color: #555;

    
    

}

.content h3 {
    color: #222;
    font-size: 18px;
    font-weight: 700;
}

.contact-wrapper {
    display: grid;
    grid-template-columns: 60px 200px;
    grid-template-rows: repeat(1, 1fr);
    margin-bottom: 15px;
}

.contact-wrapper > *:first-child {
    grid-row: 1 / span 2;
    align-self: center;
    justify-self: start;
}

.contact-wrapper span {

    font-size: 18px;
    color: #222;
    font-weight: 500;
}

.contact-wrapper h3,
.contact-wrapper a {
    color: #222;
    font-size: 14px;
    font-weight: 500;
}

.heading h3 {
    color: #222;

    font-size: 18px;
    margin-top: -10px;
}

/* Media Queries */

/* Küçük ekranlar için medya sorgusu */
@media screen and (max-width: 767px) {
    .contact-form {
        max-width: 100%;
    }
}

/* Orta ekranlar için medya sorgusu */
@media screen and (min-width: 768px) and (max-width: 1023px) {
    .contact-form {
        max-width: 768px;
        margin: 0 auto; /* Formu ortalamak için margin eklendi */
    }
}

/* Daha büyük ekranlar için medya sorgusu */
@media screen and (min-width: 1024px) {
    .contact-form {
        max-width: 1024px;
        margin: 0 auto; /* Formu ortalamak için margin eklendi */
    }
}